import type { Action, Controller } from '@ali/4ever-cangjie';

import { PARAGRAPH_SET_COLOR, PARAGRAPH_SET_BG_COLOR, PARAGRAPH_SELECT_RANGE } from '../actions';
import { setParagraphBgColor, setParagraphColor, selectRange } from '../commands';
import { ISetParagraphColor, ISetParagraphBgColor, ISelectRange } from './../types';

const onAction = (action: Action, controller: Controller, next: Function) => {
  const { type, payload } = action;

  switch(type) {
    case PARAGRAPH_SET_COLOR:
      return controller.command(setParagraphColor, payload as ISetParagraphColor);
    case PARAGRAPH_SET_BG_COLOR:
      return controller.command(setParagraphBgColor, payload as ISetParagraphBgColor);
    case PARAGRAPH_SELECT_RANGE:
      return controller.command(selectRange, payload as ISelectRange);
    default:
      return next();
  }
};

export default onAction;
